<template>
  <el-dialog
    title="指派骑手"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form>
      <el-form-item>
        <el-select v-model="selectRiderId" placeholder="请选择骑手">
          <el-option
            v-for="item in riderList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="onConfirm">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { MIXINS_QUERYRIDERS_API, MIXINS_ZHIDING_API } from '@/mixins/api.js'

export default {
  name: 'assignedCmp',

  mixins: [
    MIXINS_QUERYRIDERS_API,
    MIXINS_ZHIDING_API
  ],

  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    },
    orderData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },

  data() {
    return {
      riderList: [], // 骑手列表
      selectRiderId: '', // 选择的骑手Id
    }
  },

  created () {
    this.apiQueryRiders(localStorage.shopId)
  },

  methods: {
    handleClose() {
      this.$emit('close')
    },
    // 获取骑手列表
    apiQueryRiders (shopId) {
      const data = shopId
      this.queryRiders(data).then((res) => {
        console.log(res)
        if (res.data.success) {
          this.riderList = res.data.data
        } else {
          this.$message.error(res.data.message)
        }
      })
    },
    // 点击确定
    onConfirm () {
      const data = {
        selectRiderId: this.selectRiderId,
        orderId: this.orderData.id,
        shopId: localStorage.shopId
      }
      this.zhiDing(data).then((res) => {
        if (res.data.success) {
          this.$message.success('指派成功')
          this.handleClose()
        } else {
          this.$message.error(res.data.message)
        }
      })
    }
  }
}
</script>

