<template>
  <div>
    <LayoutBox one="订单列表">
      <h4 class="title">订单列表</h4>
      <div class="tool">
        <div class="tool_select">
          <span class="fs-lg mr-1">下单时间</span>
          <el-date-picker v-model="startTime" type="date" placeholder="选择日期" size="mini"></el-date-picker>至<el-date-picker v-model="endTime" type="date" placeholder="选择日期" size="mini"></el-date-picker>
        </div>
        <div class="mt-2 mb-2">
          <el-select v-model="searchType" class="mr-1" placeholder="商品名称" size="small" style="width: 112px">
            <el-option label="商品名称" value="prodName"></el-option>
            <el-option label="订单号" value="orderCode"></el-option>
          </el-select>
          <el-input v-model="searchInput" placeholder="请输入内容" size="small" style="width: 240px"></el-input>
        </div>
        <div class="btn_search">
          <el-button class="inquire1" type="success" size="small" @click="onSearch">查询</el-button>
          <el-button size="small">重置</el-button>
        </div>
      </div>
      <div class="menu_box">
        <el-menu default-active="" class="el-menu-demo mb-2" mode="horizontal" @select="onNav">
          <el-menu-item index="">全部</el-menu-item>
          <el-menu-item index="1">已下单</el-menu-item>
          <el-menu-item index="3">待评价</el-menu-item>
          <el-menu-item index="10">已完成</el-menu-item>
          <el-menu-item index="9">已关闭</el-menu-item>
        </el-menu>
        <el-button class="screen_btn" type="primary" @click="onScreen">大屏展示</el-button>
      </div>
      <el-table :data="orderList" row-key="id" lazy :load="load" :tree-props="{children: 'children', hasChildren: 'hasChildren'}" max-height="400" style="width: 100%" size="mini">
        <el-table-column prop="orderCode" label="订单号" width="180" align="center"></el-table-column>
        <el-table-column prop="prodName" label="商品名称" width="180" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createTime" label="下单时间" align="center" width="170"></el-table-column>
        <el-table-column prop="userName" label="买家" align="center"></el-table-column>
        <el-table-column label="交易状态" align="center" width="130">
          <!-- 订单状态 1-已下单 3-待评价 9-已关闭 10-已完成-->
          <template slot-scope="scope">
            <div class="trading_status">
              <span v-if="scope.row.orderStatus === 1 && scope.row.postType === 5" style="color: #e39025">待自提</span>
              <span v-else-if="scope.row.orderStatus === 1" style="color: #e39025">待揽件</span>
              <span v-else-if="scope.row.orderStatus === 2" style="color: #666666">配送中</span>
              <span v-else-if="scope.row.orderStatus === 3" style="color: #666666">待评价</span>
              <span v-else-if="scope.row.orderStatus === 10" style="color: #666666">已完成</span>
              <span v-else-if="scope.row.orderStatus === 9" style="color: #999999">已关闭</span>
              <router-link v-if="!scope.row.prodItemId" :to="{ path: '/superMarket/orderDetail', query: { id: scope.row.id } }" style="color: #459ae9">详情</router-link>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="count" label="明细数量" align="center"></el-table-column>
        <el-table-column prop="price" label="单价" align="center"></el-table-column>
        <el-table-column label="实收金额" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.payAmount">￥{{ scope.row.payAmount }}</span>
          </template>
        </el-table-column>
        <el-table-column label="骑手" align="center" show-overflow-tooltip>
          <template slot-scope="scope">
            <span>{{ scope.row.courierName !== '0' ? scope.row.courierName : '' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="120">
          <template slot-scope="scope">
            <code-cmp v-if="scope.row.orderStatus === 1 && scope.row.postType === 5" :orderId="scope.row.id" />
            <el-button v-else-if="scope.row.orderStatus === 2" size="mini" type="text" @click="onConfirm(scope)">确认送达</el-button>
            <!-- <el-button v-if="scope.row.orderStatus === 1" size="mini" type="text" @click="onAssignedDialog(scope)">指派</el-button> -->
            <el-button v-if="scope.row.orderStatus === 1" size="mini" type="text" @click="onOrderClose(scope)">取消订单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="orderTotle" @current-change="handleCurrentChange"></el-pagination>
    </LayoutBox>

    <assigned-cmp :orderData="selectOrderData" :dialogVisible="assignedDialog" @close="assignedDialog = false" />

  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import assignedCmp from './cmp/assigned.vue'
import codeCmp from './cmp/code.vue'

export default {
  name: 'OrderList',

  components: {
    LayoutBox,
    assignedCmp,
    codeCmp,
  },
  /*eslint-disable*/
  data () {
    return {
      orderList: [], // 订单列表
      orderTotle: 0, // 订单总条数
      pageNo: 1, // 当期页数默认是1
      orderStatus: '', // 选择订单状态
      startTime: '', // 选择下单开始时间
      endTime: '', // 选择下单结束时间
      searchType: '', // 搜索类型
      searchInput: '', // 搜索输入
      goodsDataYJM: [], // 自定义字段商品信息
      tableLoading: false, // 表格的loading
      assignedDialog: false, // 指派窗口
      selectOrderData: {}, // 选择指派的订单
    }
  },

  mounted () {
    this._getOrderList()
  },

  methods: {
    // 点击导航栏
    onNav (key, keyPath) {
      this.orderStatus = key
      this._getOrderList()
    },
    // 分页
    handleCurrentChange (val) {
      this.pageNo = val
      this._getOrderList()
    },
    // 表格明细懒加载
    load(tree, treeNode, resolve) {
      this._getOrderItem(tree.id)
        .then((res) => {
          resolve(res.data.data.list)
        })
    },
    // 点击取消订单
    onOrderClose (scope) {
      this.$confirm('此操作将导致订单失效, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.apiOrderClose(scope)
      })
    },
    // 点击查询
    onSearch () {
      this._getOrderList()
    },
    // 点击确认
    onConfirm (scope) {
      this.$confirm('订单确定送达了吗? 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.apiOrderFinish(scope)
      })
    },
    // 点击指派
    onAssignedDialog (scope) {
      this.selectOrderData = scope.row
      this.assignedDialog = true
    },
    // 点击大屏展示
    onScreen () {
      var { href } = this.$router.resolve({
        path: '/superMarket/orderScreen',
      })
      window.open(href)
    },

    // 私有方法-----------------------------------------------------------
    // 获取订单列表
    _getOrderList () {
      let data = {
        pageNo: this.pageNo,
      }
      // 模糊查询
      Object.assign(data, { [this.searchType]: this.searchInput })
      data.startTime = this.startTime,
      data.endTime = this.endTime,
      data.orderStatus = this.orderStatus
      this.$http.post('/woho/order/head/query', data)
        .then((res) => {
          this.orderTotle = res.data.data.total
          res.data.data.list.map((item) => {
            item.hasChildren = true
          })
          this.orderList = res.data.data.list
        })
    },
    // 获取订单明细列表
    _getOrderItem (id) {
      const DATA = {
        pageSize: 10000,
        orderHeaderId: id
      }
      return this.$http.post('/woho/order/item/query', DATA)
        .then((res) => {
          return res
        })
    },
    // 订单关闭
    apiOrderClose (scope) {
      this.$http.post(`/woho/order/head/orderClose/${scope.row.id}`)
        .then((res) => {
          console.log(res)
          this.$message.success('取消成功')
          scope.row.orderStatus = 9
        })
    },
    // 订单完成
    apiOrderFinish (scope) {
      this.$http.post(`/woho/order/head/orderFinish/${scope.row.id}`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('操作成功')
            scope.row.orderStatus = 3
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .title{
    font-size: 19px;
    margin-top: 0px;
  }
  .tool{
    position: relative;
    .tool_select{
      display: flex;
    }
    .btn_search{
      position: absolute;
      bottom: 0px;
      right: 0px;
    }
  }
  .menu_box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .screen_btn{
      height: 40px;
    }
  }
  .trading_status{
    display: flex;
    flex-direction: column;
  }
</style>
