<template>
  <el-popover
    v-model="visible"
    placement="top"
    width="160"
  >
    <el-form :model="form">
      <el-form-item label="自提码">
        <el-input v-model="form.doorCode" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div style="text-align: right; margin: 0">
      <el-button size="mini" type="text" @click="visible = false">取消</el-button>
      <el-button type="primary" size="mini" @click="orderCheck">确定</el-button>
    </div>
    <el-button slot="reference" size="mini" type="text">自提码</el-button>
  </el-popover>
</template>

<script>
export default {
  name: 'code_cmp',

  props: {
    orderId: {
      type: String,
      default: '',
    }
  },
  
  data () {
    return {
      form: {
        doorCode: '',
      },
      visible: false,
    }  
  },

  methods: {
    // 点击确定
    async orderCheck () {
      const data = {
        ...this.form,
        id: this.orderId,
      }
      const res = await this.$http.post(this.$api.orderCheck, data)
      if (res.data.success) {
        this.$message.success('取件成功')
        this.visible = false
        this.reload()
      } else {
        this.$message.error(res.data.msg)
      }
    }
  },
  
  inject: ['reload']
}
</script>

